import axios from './axios';

// 投票列表
export function getVoteList(option) {
  const url = '/weixin/vote/lists';

  return axios.post(url, option);
}
// 投票详情
export function getVoteDetail(option) {
  const url = '/weixin/vote/detail';

  return axios.post(url, option);
}

// 提交投票
export function submitVote(option) {
  const url = '/weixin/vote/answer';

  return axios.post(url, option);
}

// 投票统计 -- 管理员
export function getVoteStatistics(option) {
  const url = '/weixin/vote/statis';

  return axios.post(url, option);
}

// 已参与人员 -- 管理员
export function getVoteHadJoin(option) {
  const url = '/weixin/vote/getJoinMem';

  return axios.post(url, option);
}

// 未参与人员 -- 管理员
export function getVoteNotJoin(option) {
  const url = '/weixin/vote/getUnjoinMem';

  return axios.post(url, option);
}

// 提醒参与 -- 管理员
export function remindVote(option) {
  const url = '/weixin/vote/remind';

  return axios.post(url, option);
}



